import React from "react";

import classes from "./HPMid.module.scss";

import Step1 from "../../icons/HPMid/step-1.svg";
import Step2 from "../../icons/HPMid/step-2.svg";
import Step3 from "../../icons/HPMid/step-3.svg";
import ArrowRight from "../../icons/HPMid/arrow-right.svg";
import ArrowLeft from "../../icons/HPMid/arrow-left.svg";
import ReviewCard from "../ReviewCard/ReviewCard";

const HPMid = () => {
  return (
    <div className={classes.page}>
      <section className={classes["how-it-works"]}>
        <h2 className={classes["section-heading"]}>How it works</h2>

        <div className={classes.steps}>
          <div className={classes.step}>
            <img src={Step1} alt="icon" className={classes["step-icon"]} />
            <h3 className={classes["step-name"]}>Place your business</h3>
            <p className={classes["step-description"]}>
              Describe what needs <br /> to be done and when
            </p>
          </div>
          <div className={classes["arrow-wrapper"]}>
            <img src={ArrowRight} alt="arrow" className={classes.arrow} />
          </div>
          <div className={classes.step}>
            <img src={Step2} alt="icon" className={classes["step-icon"]} />
            <h3 className={classes["step-name"]}>Do your best </h3>
            <p className={classes["step-description"]}>
              Choose the right specialist <br /> for the job
            </p>
          </div>
          <div className={classes["arrow-wrapper"]}>
            <img src={ArrowRight} alt="arrow" className={classes.arrow} />
          </div>
          <div className={classes.step}>
            <img src={Step3} alt="icon" className={classes["step-icon"]} />
            <h3 className={classes["step-name"]}>Collect stars</h3>
            <p className={classes["step-description"]}>
              Be on top of services to <br /> get more orders
            </p>
          </div>
        </div>
      </section>

      <section className={classes.cta}>
        <div className={classes["cta-wrapper"]}>
          <div className={classes["cta-text"]}>
            <h2 className={classes["cta-heading"]}>
              Create an order now and find an expert in minutes!
            </h2>
            <h3 className={classes["cta-subheading"]}>
              Kabanchik - thousands of specialists on the platform
            </h3>
          </div>
          <button className={classes["cta-button"]}>Create an order</button>
        </div>
      </section>

      <section className={classes.reviews}>
        <header className={classes["reviews-header"]}>
          <h2 className={classes["section-heading"]}>Customers reviews</h2>
          <div className={classes["reviews-buttons"]}>
            <button
              className={`${classes["btn-review-prev"]} ${classes["btn-review"]} `}
            >
              <img src={ArrowLeft} alt="prev" />
            </button>
            <button
              className={`${classes["btn-review-next"]} ${classes["btn-review"]} `}
            >
              <img src={ArrowRight} alt="next" />
            </button>
          </div>
        </header>
        <div className={classes["review-cards"]}>
          <ReviewCard />
          <ReviewCard />
          <ReviewCard />
        </div>
      </section>
      <section className={classes.geography}>
        <h2 className={classes["section-heading"]}>Customers reviews</h2>

        <ul className={classes.cities}>
          <li className={classes.city}>Horceg Novi</li>
          <li className={classes.city}>Cetinje</li>
          <li className={classes.city}>Rozaje</li>
          <li className={classes.city}>Horceg Novi</li>
          <li className={classes.city}>Budva</li>
          <li className={classes.city}>Niksic</li>
          <li className={classes.city}>abljak</li>
          <li className={classes.city}>Budva</li>
          <li className={classes.city}>Ulcinj</li>
          <li className={classes.city}>Pljevlja</li>
          <li className={classes.city}>Kolasin</li>
          <li className={classes.city}>Ulcinj</li>
          <li className={classes.city}>Kotor</li>
          <li className={classes.city}>Berane</li>
          <li className={classes.city}>Mojkovac</li>
          <li className={classes.city}>Kotor</li>
          <li className={classes.city}>Podgorica</li>
          <li className={classes.city}>Bijelo Polje</li>
          <li className={classes.city}>Podgorica</li>
          <li className={classes.city}>Podgorica</li>
          <li className={classes.city}>Tivat</li>
          <li className={classes.city}>Bar</li>
          <li className={classes.city}>Pluzine</li>
          <li className={classes.city}>Tivat</li>
        </ul>
      </section>
    </div>
  );
};

export default HPMid;
