import React, { useEffect, useState } from "react";

import classes from "./HPOptions.module.scss";

import Homebuilder from "../../icons/HPOptions/homebuilder.svg";
import Finishing from "../../icons/HPOptions/finishing.svg";
import Cleaning from "../../icons/HPOptions/cleaning.svg";
import Courier from "../../icons/HPOptions/courier.svg";
import Building from "../../icons/HPOptions/building.svg";
import Repair from "../../icons/HPOptions/repair.svg";
import Logistics from "../../icons/HPOptions/logistics.svg";
import Household from "../../icons/HPOptions/household.svg";

const HPOptions = () => {
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);
  const [isLineBreak, setIslineBreak] = useState(false);

  //choose the screen size
  const handleResize = () => {
    setScreenWidth(window.innerWidth);
    if (screenWidth <= 1100) {
      setIslineBreak(true);
      console.log("break");
    } else {
      setIslineBreak(false);
    }
  };

  useEffect(() => {
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  });
  useEffect(() => {
    handleResize();
  }, [screenWidth]);

  const iconsArr = [
    Homebuilder,
    Finishing,
    Cleaning,
    Courier,
    Building,
    Repair,
    Logistics,
    Household,
  ];

  const data = [
    { icon: Homebuilder, name: "Homebuilder" },
    { icon: Finishing, name: "Finishing work" },
    { icon: Cleaning, name: "Cleaning services" },
    { icon: Courier, name: "Courier services" },
    { icon: Building, name: "Building work" },
    { icon: Repair, name: "Repair Techniques" },
    { icon: Logistics, name: "Logistics services" },
    { icon: Household, name: "Household Services" },
  ];

  const optionsArr = [
    "Homebuilder",
    "Finishing work",
    "Cleaning services",
    "Courier services",
    "Building work",
    "Repair Techniques",
    "Logistics services",
    "Household Services",
  ];

  return (
    <div className={classes.page}>
      <div className={classes.container}>
        <h2 className={classes.heading}>
          You can find a service that is just right for you
        </h2>
        <div className={classes.underline}>
          <span className={classes.subheading}>
            Over 100,000 verified professionals to perform your household or
            business tasks
          </span>

          {!isLineBreak && (
            <div className={classes["help-block"]}>
              <span className={classes["help-text"]}>
                Problems?
                <br /> We can help you
              </span>
              <button className={classes["btn-help"]}>I need help</button>
            </div>
          )}
        </div>
        <section className={classes.cards}>
          {data.map((e) => (
            <div className={classes.card}>
              <img src={e.icon} alt="icon" className={classes.icon} />
              <span className={classes.name}>{e.name}</span>
              <ul className={classes.list}>
                <li className={classes.option}>
                  <span className={classes.specialist}>Plumber</span>
                  <span className={classes.count}>8689</span>
                </li>
                <li className={classes.option}>
                  <span className={classes.specialist}>Electrician</span>
                  <span className={classes.count}>9689</span>
                </li>
                <li className={classes.option}>
                  <span className={classes.specialist}>Man for hour</span>
                  <span className={classes.count}>19500</span>
                </li>
              </ul>
              <button className={classes["btn-all"]}>View All</button>
            </div>
          ))}
        </section>
        {isLineBreak && (
          <div className={classes["help-block"]}>
            <span
              className={classes["help-text"]}
              style={{ textAlign: "left" }}
            >
              Problems?
              <br /> We can help you
            </span>
            <button className={classes["btn-help"]}>I need help</button>
          </div>
        )}
      </div>
    </div>
  );
};

export default HPOptions;
