import React, { useEffect, useState, useTransition } from "react";

import classes from "./MainNav.module.scss";

import Logo from "../../icons/logo.svg";
import LogoSmall from "../../icons/logo-small.svg";
import Search from "../../icons/search.svg";
import Arrow from "../../icons/arrow.svg";
import User from "../../icons/user.svg";
import Message from "../../icons/message.svg";
import { useSelector } from "react-redux";

const MainNav = () => {
  const [selected, setSelected] = useState("");
  const [inputVal, setInputVal] = useState("");
  const [isSuggestions, setIsSuggestions] = useState(false);
  const [isOnlySearch, setIsOnlySearch] = useState(false);
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);

  const [isPending, startTransition] = useTransition();

  const handleResize = () => {
    setScreenWidth(window.innerWidth);
    console.log("resize");
  };

  useEffect(() => {
    console.log(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  });

  const handleSelect = (e) => {
    setSelected(e.target.value);
    setInputVal(e.target.value);
  };
  const handleInput = (e) => {
    setInputVal(e.target.value);
  };

  const setInput = (val) => {
    setInputVal(val);
    console.log("hi");
  };
  const handleFocus = () => {
    setIsSuggestions((prev) => !prev);
    if (screenWidth < 1000) setIsOnlySearch(true);
  };
  const handleBlur = () => {
    setTimeout(() => {
      setIsSuggestions((prev) => !prev);
    }, 100);

    setIsOnlySearch(false);
  };

  const servicesArr = useSelector((state) => state.services.services);
  return (
    <nav className={classes["main-nav"]}>
      <img src={screenWidth > 700 ? Logo : LogoSmall} alt="logo" />
      {!isOnlySearch && (
        <div className={classes["select-order-wrapper"]}>
          <select className={classes["select-order"]} onChange={handleSelect}>
            <option value="" disabled selected hidden>
              {screenWidth > 700 && "Place an order"}
              {screenWidth <= 700 && "Categories"}
            </option>
            {servicesArr.map((e, i) => (
              <option>{e}</option>
            ))}
          </select>
          <img src={Arrow} alt="arrow" className={classes.arrow} />
        </div>
      )}
      <div className={classes.search}>
        <input
          type="text"
          className={classes["search-input"]}
          placeholder="what needs to be done?"
          value={inputVal}
          onChange={handleInput}
          onFocus={handleFocus}
          onBlur={handleBlur}
        />
        <img src={Search} alt="search" className={classes["search-icon"]} />
        <div className={classes.suggestions}>
          {isSuggestions &&
            servicesArr.map((val) => (
              <div
                className={classes.suggestion}
                onClick={(e) => {
                  setInput(val);
                }}
              >
                {val}
              </div>
            ))}
        </div>
      </div>
      {!isOnlySearch && (
        <>
          <button className={classes["btn-find"]}>Find an expert</button>
          <div className={classes["select-language-wrapper"]}>
            <select className={classes["select-language"]}>
              <option value="Eng" selected>
                Eng
              </option>
              <option value="Ger">Ger</option>
              <option value="Esp">Esp</option>
            </select>
            <img src={Arrow} alt="arrow" className={classes.arrow} />
          </div>
          <button className={classes["btn-sign-in"]}>Sign In</button>
          <button className={classes["btn-become-expert"]}>
            Become an expert
          </button>
        </>
      )}
      {screenWidth <= 700 && (
        <div className={classes["mobile-icons"]}>
          <img src={Message} alt="message" />
          <img src={User} alt="user" />
        </div>
      )}
    </nav>
  );
};

export default MainNav;
