import React from "react";

import classes from "./HPHero.module.scss";

import Plumber from "../../icons/HPHero/man-on-arc.png";
import Arc from "../../icons/HPHero/arcfull.png";
import Sphere from "../../icons/HPHero/sphere.jpg";
// import Plumber from "../../icons/HPHero/plumber.jpg";

const HPHero = () => {
  return (
    <div className={classes.page}>
      <section className={classes.hero}>
        {/* <img src={Sphere} alt="sphere" className={classes.sphere} /> */}
        {/* <div className={classes.image}> */}
        {/* <img src={Arc} alt="arc" className={classes.arc} /> */}

        {/* </div> */}
        <div className={classes.left}>
          <h2 className={classes.heading}>
            Help with choosing the right specialist?
          </h2>
          <p className={classes.subheading}>
            Hurry up and create an order and within1 0 minutes we will find the
            best specialist.
          </p>
          <button className={classes["btn-submit"]}>Submit a request</button>
        </div>
        <img src={Plumber} alt="plumber" className={classes.plumber} />
      </section>
    </div>
  );
};

export default HPHero;
