import React from "react";

import classes from "./ReviewCard.module.scss";

import StarEmpty from "../../icons/ReviewCard/StarEmpty.svg";
import StarGold from "../../icons/ReviewCard/StarGold.svg";
import David from "../../icons/ReviewCard/david.svg";

const ReviewCard = () => {
  return (
    <div className={classes.card}>
      <div className={classes.user}>
        <img src={David} alt="user" />
        <div className={classes["user-info"]}>
          <span className={classes["user-name"]}>David</span>
          <span className={classes["user-position"]}>Customer</span>
        </div>
      </div>
      <h2 className={classes.heading}>Needed a copywriter for grocery store</h2>
      <p className={classes.message}>
        Kabanchik is a godsend! In the conditions of the big city, running
        around and the eternal balancing of "how much is my time worth" for me
        Kabanchik is a wand-helper. 3 minutes to create an order, 1 phone call
        with a specialist, about an hour of his work and everyone is happy!
      </p>
      <div className={classes["stars-block"]}>
        <div className={classes.stars}>
          <img src={StarGold} alt="star" />
          <img src={StarGold} alt="star" />
          <img src={StarGold} alt="star" />
          <img src={StarGold} alt="star" />
          <img src={StarEmpty} alt="star" />
        </div>

        <span className={classes.rating}>4.00</span>
      </div>
    </div>
  );
};

export default ReviewCard;
