import React, { useEffect, useState } from "react";

import classes from "./Page.module.scss";

import DoubleInput from "../DoubleInput/DoubleInput";
import Search from "../../icons/search-white.svg";
import Plus from "../../icons/plus.svg";
import People from "../../icons/people2.png";
import Woman from "../../icons/woman.jpg";
import Man from "../../icons/man.jpg";
import Hands from "../../icons/hands.jpg";

import Promo from "../../icons/promo.svg";
import PurpleCircle from "../../icons/purple_circle.svg";
import CoralCircle from "../../icons/coral_circle.svg";

const Page = () => {
  const [isMainColumn, setIsMainColumn] = useState(false);

  //choose the screen size
  const handleResize = () => {
    if (window.innerWidth <= 818) {
      setIsMainColumn(true);
    } else {
      setIsMainColumn(false);
    }
  };

  useEffect(() => {
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  });

  return (
    <div className={classes.page}>
      <section className={classes.main}>
        {!isMainColumn && <div className={classes.background}></div>}
        <div className={classes.left}>
          {isMainColumn && <div className={classes.background}></div>}
          <div className={classes["left-content"]}>
            <span className={classes["ads-count-label"]}>
              Total ads on the site
            </span>
            <span className={classes["ads-count-number"]}>1,342,356</span>
            <h1 className={classes.heading}>Online service ordering service</h1>
            <span className={classes.subheading}>
              Over 100,000 verified professionals to perform your household or
              business tasks
            </span>
            <DoubleInput />
          </div>
          {/* {!isMainColumn && <DoubleInput />} */}
        </div>
        <div className={classes["img-block"]}>
          {/* <img src={Woman} alt="woman" className={classes.woman} />
          <img src={Man} alt="man" className={classes.man} />
          <img src={Hands} alt="hands" className={classes.hands} /> */}
          <img src={People} alt="people" className={classes.people} />

          <div className={classes.promo}>
            <div className={classes["promo-row-1"]}>
              <div className={classes["promo-box-1"]}></div>
              <div className={classes["promo-box-2"]}></div>
            </div>
            <div className={classes["promo-row-2"]}>
              <div className={classes["promo-data-block-1"]}>
                <img
                  src={PurpleCircle}
                  alt="circle"
                  className={classes.circle}
                />
                <div className={classes["promo-values"]}>
                  <span className={classes["promo-label"]}>New jobs daily</span>
                  <span className={classes["promo-value"]}>500+</span>
                </div>
              </div>
              <div className={classes["promo-data-block-2"]}>
                <img
                  src={CoralCircle}
                  alt="circle"
                  className={classes.circle}
                />
                <div className={classes["promo-values"]}>
                  <span className={classes["promo-label"]}>
                    Happy customers
                  </span>
                  <span className={classes["promo-value"]}>100 000</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <img src={Promo} alt="promo" className={classes.promo1} /> */}
      </section>
      {/* {isMainColumn && <DoubleInput />} */}
    </div>
  );
};

export default Page;
