import React from "react";

import classes from "./Footer.module.scss";

import Kabanchik from "../../icons/Footer/kabanchik.svg";
import Facebook from "../../icons/Footer/facebook.svg";
import Insta from "../../icons/Footer/Instagram.svg";

const Footer = () => {
  return (
    <section className={classes["section-footer"]}>
      <div className={classes.content}>
        <div className={classes.contacts}>
          <img src={Kabanchik} alt="logo" className={classes.logo} />
          <div className={classes.social}>
            <img src={Facebook} alt="logo" />
            <img src={Insta} alt="logo" />
          </div>
          <span className={classes.email}>kabanchik@gmail.com</span>
        </div>
        <div className={classes.columns}>
          <div className={classes.column}>
            <h3 className={classes["col-name"]}>About us</h3>
            <ul className={classes.links}>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  About project
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Contacts
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Diary
                </a>
              </li>
            </ul>
          </div>
          <div className={classes.column}>
            <h3 className={classes["col-name"]}>How does it work</h3>
            <ul className={classes.links}>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  How to place an order
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Work in your country
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Benefits for companies
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Guarantee and security
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Secure transaction
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Latest reviews
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Top specialists
                </a>
              </li>
            </ul>
          </div>
          <div className={classes.column}>
            <h3 className={classes["col-name"]}>Help</h3>
            <ul className={classes.links}>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  FAQ
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Public offer
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Privacy policy
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Customer Service
                </a>
              </li>
            </ul>
          </div>
          <div className={classes.column}>
            <h3 className={classes["col-name"]}>Helpful Links</h3>
            <ul className={classes.links}>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  All prices
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  All categories of services
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  All categories of work
                </a>
              </li>
              <li className={classes["link-wrap"]}>
                <a href="" className={classes.link}>
                  Popular Services
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <footer className={classes.footer}> Kabanchik.ua, 2012-2022</footer>
    </section>
  );
};

export default Footer;
